@import url('https://fonts.googleapis.com/css?family=Oswald|PT+Sans');

body {
 margin: 0;
 padding: 0;
}

body, input, textarea, button{
 font-family: 'PT Sans', sans-serif;
}

h1, h2, h3, h4{
 font-family: 'Oswald', sans-serif;
}

html {
 box-sizing: border-box;
}

*, *:before, *:after {
 box-sizing: inherit;
}

img{
  max-width: 100%;
  height: auto;
}

.App {
  text-align: center;
  background-color: #31dfb7;
  display: table;
  width: 100%;
}

.App-sidebar-wrapper{
  width: 5%;
  float: left;  
  padding-top: 7%;
}

.App-sidebar-wrapper .left-sidebar-menu a {
  background: #50E3C2;
  color: #ffffff;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 25px;
}

.App-sidebar-wrapper .left-sidebar-menu .is-active {
  background: #292929;
}
 
.App-sidebar-wrapper .bar {
  padding: 20px 10px;
  background: #282c34;
}

.App-content-wrapper  {
  width: 95%;
  float: left;
  padding: 3% 5%;
  box-sizing: border-box;
  background: #282c34;
}
.hamburger_icon {
  width: 25px;
  height: 3px;
  display: block;
  margin-bottom: 3px;
  margin-left: 12px;
  background: white;
}